  computed: {
    myData() {
      const vm = this;
      let arr;

      if (vm.town === "請選擇鄉鎮市區") {
        arr = this.maskData.filter(function(item) {
          let address = item.properties.address;
          return (
            address.indexOf(vm.city) !== -1 &&
            (item.properties.mask_adult !== 0 ||
              item.properties.mask_child !== 0)
          );
        });
      } else {
        let arr2 = this.maskData.filter(function(item) {
          let address = item.properties.address;
          return (
            address.indexOf(vm.city) !== -1 &&
            (item.properties.mask_adult !== 0 ||
              item.properties.mask_child !== 0)
          );
        });

        arr = arr2.filter(item => {
          return item.properties.address.indexOf(vm.town) !== -1;
        });
      }

      return arr;
    },
    townOption() {
      const vm = this;
      let ct = vm.city;
      let arr = vm.areaData.filter(item => {
        return item.city === vm.city;
      });
      return arr;
    }
  },
  created() {
    this.getData();
  }
};
